const state = {
    // 推送显示的消息
    logmsg:{
        count:0,
        type:"info",
        message:"初始消息",
        show:false,
    },
    // 全局进度条
    progress:{
        value:0,
        count:0,
        show:true,
    },
}

const actions = {
    
}

const getters = {
    pull_log_msg:state=>state.logmsg,
    pull_progress:state=>state.progress,
}
    
const mutations = {
    // 添加新事件
    push_log_msg(state,payload){
        // console.log("添加新消息");
        payload.count=state.logmsg.count+1;
        state.logmsg=payload;
    },
    // 更新进度条
    push_progress(state,payload){
        payload.count=state.progress.count+1;
        state.progress=payload;
    }
}

export default {
    state,
    actions,
    getters,
    mutations
}