// Utilities
import { make } from 'vuex-pathify'

const state = {
  currentVersion: null,
  drawer: null,
  isLoading: false,
  releases: [],
  showlog:false,
}

const mutations = make.mutations(state)

const actions = {}

const getters = {
  breadcrumbs (state, getters, rootState) {
    if (!rootState.route) return []

    const path = rootState.route.path
    const text = path

    return [
      {
        text: text,
        to: text ? `/${text}` : undefined,
        disabled: !text,
      },
      {
        text: text,
        to: path,
        disabled: true,
      },
    ]
  },
  
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
