// 消息管理中心 包括显示消息 进度条 错误信息等


import { make } from 'vuex-pathify'

const state = {
    // 日志文本
    logtext:"",
    max_log_lines:10,
    msg:{},
    msgs:[],
    msg_timeout:5000,
    msg_needshow:false,
}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('msg'),
    ...make.actions('msgs'),
    ...make.actions('logtext'),
    ...make.actions('msg_needshow'),
   
    // 更新消息
    push_msg({dispatch},payload){
        let max_log_lines=state.max_log_lines;
        let msgs =state.msgs;
        if (payload.type){
            payload.color=payload.type;
        }
        if (payload.message){
            payload.text=payload.message;
        }
        msgs.push(payload.color+":"+payload.text)
        if (msgs.length>max_log_lines){
            msgs = msgs.slice(msgs.length-max_log_lines,msgs.length);
        }
        dispatch("setMsg",payload);
        dispatch("setMsg_needshow",true);
        dispatch("setMsgs",msgs);
        dispatch("setLogtext",msgs.join("\n"));
    },

    // 更新消息显示状态
    update_msg_needshow({dispatch},payload){
        dispatch("setMsg_needshow",payload);
    }
}

const getters = {
    msg_color:state=>{
        if (state.msg.color){
            return state.msg.color;
        }else{
            return state.msg.type;
        }
    },
    msg_text:state=>{
        return state.msg.text;
    }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
