import axios from 'axios';
// 常量
export const WEB_ARI_BASE_URL="/api/web";
export const WEB_TIME_OUT=10000;
// 导出变量
let REQ_TOKEN="";
let PIC_CODE="";
let EMAIL_CODE="";
export{REQ_TOKEN,PIC_CODE,EMAIL_CODE};

export const webService = axios.create({
    baseURL:WEB_ARI_BASE_URL,   // api的base_url
    timeout: WEB_TIME_OUT       // 请求超时时间
  });

// request拦截器，实现loading加载
webService.interceptors.request.use(config => {
    config.headers.token = REQ_TOKEN;  //将token设置成请求头
    return config
    }, error => {
    return Promise.reject(error)
    })
    
// response拦截器，实现loading关闭
webService.interceptors.response.use(data => {
return data
}, error => {
return Promise.reject(error)
})


// get 登陆状态查询
export const GetLoginStatus = () => { return webService.get(`/loginstatus`).then(res => res.data); };
// get 获取用户信息
export const GetUserInfo = () => { return webService.get(`/userinfo`).then(res => res.data); };
// get 图片验证码
export const GetPiccode = () => { return webService.get(`/piccode`).then(res => res.data); };
// post 邮箱验证码
export const GetEmailcode = params => { return webService.post(`/emailcode`,params).then(res => res.data); };
// post Web登陆
export const Login = params => { return webService.post(`/login`, params).then(res => res.data); };
// post Web注册
export const Register = params => { return webService.post(`/register`, params).then(res => res.data); };
// get Web登出
export const Logout = () => { return webService.get(`/logout`).then(res => res.data); };

// 获取用户有权限的数据库


// 获取数据库配置
export const GetCollectionConfig = (collection) => { return webService.get(`/db/getcollectionconfig`,{params:{collection:collection}}).then(res => res.data); };

// 获取分页数据
export const GetPageData=(collection,params)=>{return webService.get(`/db/${collection}/many`,{params:params}).then(res=>res.data)};