<template>
    <v-container >
        <v-layout justify-center>
        <v-card class="elevation-12">
            <v-toolbar
                color="primary"
                dark
                flat
              > 
              <v-toolbar-title >用户登陆</v-toolbar-title>
            </v-toolbar>
        
        <v-card-text>
            <v-form
            ref="form"
            v-model="valid"
            lazy-validation>
                <v-text-field
                v-model="username"
                :rules="usernameRules"
                label="用户名"
                required>
                </v-text-field>

                <v-text-field
                v-model="password"
                :append-icon="showpassword ? 'visibility' : 'visibility_off'"
                :rules="passwordRules"
                :type="showpassword ? 'text' : 'password'"
                label="密码"
                required
                @click:append="showpassword = !showpassword"
            ></v-text-field>
               
            </v-form>

        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="SubmitLogin">登陆

            </v-btn>
            <v-btn
            color="primary"
            class="mr-4"
            @click="GoRegister">注册
            </v-btn>

            <v-btn
            color="primary"
            class="mr-4"
            @click="GoForgetPassword">忘记密码

            </v-btn>
        </v-card-actions>
            
        </v-card>
        </v-layout>
    </v-container>

</template>

<script>

export default {
    name:"Login",
    data:()=>({
        valid: true,
        showpassword:false,
        username:"",
        usernameRules: [
        v => !!v || '请输入用户名!',
        v => (v && v.length >= 6) || '用户名长度需大于6',
        ],
        password:"",
        passwordRules:[
            v=> !! v || "请输入密码!",
            v => (v && v.length >= 6) || '密码长度需大于6',
        ],
        piccode:"",
        piccodeRules:[
            v=> !! v || "请输入验证码!",
        ],
    }),
    methods:{
        // 登陆 
        SubmitLogin(){
            let params ={username:this.username,password:(this.password),piccode:this.piccode };
            console.log("触发登陆按钮，调用登陆API,参数",params);
            this.$store.dispatch('user/login',params);
        },
        // 去注册页面
        GoRegister(){
            // console.log("触发注册函数，带用户跳转注册页面");
            // this.$router.push(types.ROUTE_REGISTER);
            this.$store.dispatch("user/logout");
        },
        // 重置密码
        GoForgetPassword(){
            // console.log("触发忘记密码函数，带用户跳转重置密码页面");
            // this.$router.push(types.ROUTE_RESETPASSWORD);
            this.$store.dispatch("user/update_userinfo");
            
        }

    },
    computed:{

    }
}
</script>