// 用户状态管理中心


import { make } from 'vuex-pathify'
import * as api from "@/apis/webapi"

const state = {
    islogin:false,
    // 用户信息
    userinfo:{},
}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('userinfo'),
    ...make.actions('islogin'),
    // 更新用户登陆状态
    update_loginstatus({dispatch}){
        api.GetLoginStatus().then(res=>{
            if (res.code==200){
                console.log("更新登陆状态成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
            }else{
                console.log("更新登陆状态失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 更新用户信息
    update_userinfo({dispatch}){
        api.GetUserInfo().then(res=>{
            if (res.code==200){
                console.log("更新用户信息成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setUserinfo",res.data);
            }else{
                console.log("更新用户信息失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 登陆
    login({dispatch},payload){
        api.Login(payload).then(res=>{
            if (res.code==200){
                console.log("登陆成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("update_userinfo");
                dispatch("setIslogin",true);
            }else{
                console.log("登陆失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 登出
    logout({dispatch}){
        api.Logout();
        dispatch("setIslogin",false);
        dispatch("setUserinfo",{});
    },
    // 发送邮箱验证码
    send_email_code({dispatch},payload){
        api.GetEmailcode(payload).then(res=>{
            if (res.code==200){
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
            }else{
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true});
            }
        })
    },



}

const getters = {
    // 获取集合
    collections:state=>{
        if (state.userinfo.collectionauth){
            
            let keys=Object.keys(state.userinfo.collectionauth);
            let cols =keys.map(item=>({
                text:item,value:item,
            }))
        
            return cols;
        }else{
            return [];
        }
    },
    username:state=>{
        return state.userinfo.username;
    }
}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
