// const WebSocket = require('ws');

let MyWebsocket ={};
MyWebsocket.wsurl="ws://cdn.oldking.club:2086/v1/miner/ws";
MyWebsocket.overtime=5000;
MyWebsocket.hartbeettime=1000;

MyWebsocket.onopen=function(evt){
    console.log("连接成功",MyWebsocket.wsurl,evt.data);
};
MyWebsocket.onmessage =function(evt){
    console.log(MyWebsocket.wsurl,"收到消息",evt.data);
};
MyWebsocket.onclose=function(evt){
    console.log(MyWebsocket.wsurl,"连接关闭",evt.data);
    reConnect();
};
MyWebsocket.onerror=function(evt){
    console.log("连接错误",MyWebsocket.wsurl,evt.data);
};
MyWebsocket.close=function(){
    MyWebsocket.websocket.close();
};
MyWebsocket.init=function(wsurl){
    MyWebsocket.wsUri = wsurl;
    this.lastHeartBeat = new Date().getTime();
    this.overtime=MyWebsocket.overtime;
    MyWebsocket.lastHeartBeat=new Date().getTime();
    MyWebsocket.websocket = new WebSocket(MyWebsocket.wsUri);
    MyWebsocket.websocket.onopen = MyWebsocket.onopen;
    MyWebsocket.websocket.onclose = MyWebsocket.onclose;
    MyWebsocket.websocket.onmessage = function(evt){
        MyWebsocket.lastHeartBeat = new Date().getTime();
        MyWebsocket.onmessage(evt);
    };
    MyWebsocket.websocket.onerror = MyWebsocket.onerror;
    setInterval(checkConnect,MyWebsocket.overtime);
    setInterval(ping,MyWebsocket.hartbeettime);

}

function ping(){
    if (MyWebsocket.websocket.readyState==1){
        MyWebsocket.websocket.send("PING");
    }
}

function checkConnect() {
    if ((new Date().getTime() - MyWebsocket.lastHeartBeat) > MyWebsocket.overtime) {
        console.log(new Date().getTime() ,MyWebsocket.lastHeartBeat,MyWebsocket.overtime)
        reConnect();
    } 
}

function reConnect(){
    MyWebsocket.websocket.close();
    console.log(MyWebsocket.wsurl,"连接断开，正在尝试重新建立连接");
    MyWebsocket.init(MyWebsocket.wsurl);
}
exports.WebSocket=MyWebsocket;
