import { make } from 'vuex-pathify'
import * as api from "@/apis/webapi"

const state = {
    // 当前数据库
    collection:"",
    // 显示的字段 这个用户可以前端修改 默认只显示必须字段
    show_fields:[],
    // 数据库总配置文件
    col_config_map:{},
    // 筛选条件
    query: "",
    // 筛选时的数据库值
    all_filter_values:{},
    // 集合主要配置
    collection_config:{},
    // 集合分页数据
    col_page_data:{},
    // 页面选项
    items_per_page_options:[5, 100, 500, 1000],
}

const mutations = make.mutations(state)

const actions = {
    ...make.actions('all_filter_values'),
    ...make.actions('query'),
    ...make.actions('collection'),
    ...make.actions('collection_config'), 
    ...make.actions('col_config_map'),
    ...make.actions('col_page_data'),
    // 更新筛选条件
    update_query({dispatch},payload){
        let field_types=getters.field_types(state);
        let querys =[];
        for (const v of payload){
            let field =v["field"];
            let vtype=field_types[field];
            let value =v["value"];
            let field_type=v["filter_type"];
            if (vtype){
                querys.push(field+":"+value+":"+vtype+":"+field_type)
            }
        }
        dispatch("setQuery",querys.join(","));
    },

    // 更新筛选候选数据
    update_filter_values({ dispatch },payload){
        let all_filter_values=payload.all_filter_values;
        // 重写此更新函数
        all_filter_values[payload.field]=[{text:"key1",value:"value1"},{text:"key2",value:"value2"}];
        dispatch("setAll_filter_values",all_filter_values);
    },
    // 更新分页数据
    update_col_page_data({dispatch},payload){
        let page=payload.page;
        let pagesize=payload.itemsPerPage;
        api.GetPageData(state.collection,{query:state.query,page:page,pagesize:pagesize}).then(res=>{
            if (res.code==200){
                console.log("更新分页成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                dispatch("setCol_page_data",res);
            }else{
                console.log("更新分页失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 更新数据库配置
    update_collection_config({dispatch},collection){
        console.log("更新数据库配置",collection);
        api.GetCollectionConfig(collection).then(res=>{
            if (res.code==200){
                console.log("更新数据库配置成功",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"success"},{root:true});
                let col_config_map=state.col_config_map;
                col_config_map[collection]=res.data;
                dispatch("setCol_config_map",col_config_map);
                dispatch("setCollection_config",res.data);
            }else{
                console.log("更新数据库配置失败",res);
                dispatch("msglog/push_msg",{message:res.msg,type:"error"},{root:true})
            }
        })
    },
    // 更新数据库
    update_collection({dispatch},collection){
        dispatch("setCollection",collection);
        // 清空数据表
        dispatch("setQuery","")
        let col_config =state.col_config_map[collection];
        if (col_config){
            dispatch("setCollection_config",col_config);
        }else{
            dispatch("update_collection_config",collection);
        }

    },
}

const getters = {
    ...make.getters(state),

    // 生成筛选字段数据
    filter_fields:state=>{
        if (state.collection_config.field2label){
            let field2label=state.collection_config.field2label;
            let fields = state.collection_config.indexfields.map(item=>({
                text:field2label[item],value:item
            }));
            return fields 
        }else{
            return []
    }
    },
    // 筛选字段数据类型
    field_types:state=>{
        return state.collection_config.fieldtype;
    },

    // 表头
    headers:state=>{
        if (state.collection_config.field2label){
            let field2label =state.collection_config.field2label;
            return state.collection_config.allfields.map(item=>({
                text:field2label[item],value:item,
            }))
        }else{
            return []
        }
        
    },
    // 表格内容
    tabledata:state=>{
        if (state.col_page_data.data){
            return state.col_page_data.data
        }else{
            return []
        }
    },

    // 总共行数
    total_rows:state=>{
        return state.col_page_data.total
    },
    // 页面大小
    page_size:state=>{
        return state.col_page_data.pagesize
    },


}

export default {
  namespaced: true,
  state,
  mutations,
  actions,
  getters,
}
