<template>

    <v-container fill-height> 
        <v-layout
        justify-center
        >
        <v-card class="elevation-12">
            <v-toolbar
                color="primary"
                dark
                flat
              > 
              <v-toolbar-title >用户注册</v-toolbar-title>
            </v-toolbar>
        
        <v-card-text>
            <v-form
            ref="form"
            v-model="valid"
            lazy-validation>
                <v-text-field
                v-model="username"
                :rules="usernameRules"
                label="用户名"
                required>
                </v-text-field>

                <v-text-field
                v-model="password"
                :append-icon="showpassword ? 'visibility' : 'visibility_off'"
                :rules="passwordRules"
                :type="showpassword ? 'text' : 'password'"
                label="密码"
                required
                @click:append="showpassword = !showpassword"
            ></v-text-field>

            <v-text-field
                v-model="checkpassword"
                :append-icon="showpassword ? 'visibility' : 'visibility_off'"
                :rules="checkpasswordRules"
                :type="showpassword ? 'text' : 'password'"
                label="再次输入密码"
                required
                @click:append="showpassword = !showpassword"
            ></v-text-field>

            <v-text-field
                v-model="email"
                :rules="emailRules"
                label="邮箱"
                required
            ></v-text-field>
            <v-btn
            color="primary"
            class="mr-4"
            @click="SendEmailCode">发送验证码</v-btn>

            <v-text-field
                v-model="emailcode"
                :rules="emailcodeRules"
                label="邮箱验证码"
                required
            ></v-text-field>

               
            </v-form>

        </v-card-text>
        <v-card-actions>
            <v-spacer></v-spacer>
            
            <v-btn
            color="primary"
            class="mr-4"
            @click="SubmitRegister">注册
            </v-btn>

            <v-btn
            :disabled="!valid"
            color="primary"
            class="mr-4"
            @click="GoLogin">已有账号>>登陆
            </v-btn>
        </v-card-actions>
            
        </v-card>
        </v-layout>
    </v-container>

</template>

<script>
import {WebSocket} from  "@/apis/odkWebsocket"
export default {
    name:"Register",
    data:()=>({
        valid: true,
        showpassword:false,
        username:"",
        usernameRules: [
        v => !!v || '请输入用户名!',
        v => (v && v.length >= 6) || '用户名长度需大于6',
        ],
        password:"",
        passwordRules:[
            v=> !! v || "请输入密码!",
            v => (v && v.length >= 6) || '密码长度需大于6',
        ],
        checkpassword:"",
        checkpasswordRules:[
            v=> !! v || "请输入密码!",
        ],
        email:"",
        emailRules:[
            v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || '请输入正确的邮箱'
        ],
        emailcode:"",
        emailcodeRules:[
            v=> !! v || "请输入邮箱验证码!",
        ]

    }),
    methods:{
        // 登陆 
        GoLogin(){
            this.$router.resolve({name:"Login"});
        },
        // 去注册页面
        SubmitRegister(){
           
        },
        // 发送邮箱验证码
        SendEmailCode(){
            WebSocket.init("wss://cdn.oldking.club:2096/v1/wsapi");
            
            
            
        },
    },
    computed:{

    },
}
</script>